/* Styles for Reviews and Give Review pages */

.reviews-container {
  padding: 24px;
  max-width: 1100px;
  margin: 24px auto;
}

.reviews-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.reviews-actions { display:flex; gap:8px; align-items:center; }
.btn-secondary {
  background: #28a745; /* green back button */
  color: #fff;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
}
.btn-secondary:hover { background: #218838; }
.btn-back { /* ensure GiveReview back button matches */
  background: #28a745;
  color: #fff;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
}
.btn-back:hover { background: #218838; }
.btn-logout {
  background: #dc3545;
  color: #fff;
  border: none;
  padding: 8px 10px;
  border-radius: 6px;
  cursor: pointer;
}

/* explicit logout hover to ensure red on all pages */
.btn-logout:hover {
  background: #c82333;
}

.reviews-title { margin: 0; }
.reviews-subtitle { color: #666; margin-top: 6px; }

.loading-container, .error-container, .empty-state { text-align: center; padding: 40px; }

.reviews-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 16px;
}

.review-card {
  background: #fff;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
}

.review-header { display: flex; justify-content: space-between; align-items: center; }
.reviewer-name { margin: 0; }
.reviewer-location { margin: 4px 0; color: #777; }
.review-date { color: #999; font-size: 0.85rem; }

.stars { color: #f5b50a; }
.star { margin-right: 2px; }
.star.filled { color: #f5b50a; }

.review-text { margin-top: 12px; color: #333; }

.fab-give-review {
  position: fixed;
  right: 24px;
  bottom: 90px;
  background: #28a745;
  color: #fff;
  border: none;
  padding: 12px 14px;
  border-radius: 50%;
  font-size: 18px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.12);
  cursor: pointer;
}

.btn-primary { background: #007bff; color: #fff; padding: 8px 12px; border-radius: 6px; border: none; cursor: pointer; }

/* Give Review form styles */
.give-review-container { padding: 20px; max-width: 800px; margin: 20px auto; }
.give-review-card { background: #fff; padding: 20px; border-radius: 8px; box-shadow: 0 2px 8px rgba(0,0,0,0.06); }
.user-profile-section { display:flex; gap:12px; align-items:center; }
.user-profile-avatar { width:64px; height:64px; border-radius:50%; object-fit:cover; }
.user-profile-name { margin:0; }
.form-label { display:block; margin-bottom:6px; font-weight:600; }
.form-input, .form-textarea { width:100%; padding:10px; border:1px solid #ddd; border-radius:6px; }
.rating-selector { display:flex; align-items:center; gap:8px; }
.rating-star {
  background: transparent;
  border: none;
  font-size: 28px;
  line-height: 1;
  cursor: pointer;
  color: #cfcfcf; /* lighter visible default color */
  transition: color 120ms ease, transform 120ms ease;
  padding: 2px 6px;
}
.rating-star:hover,
.rating-star:focus {
  color: #f1b50a; /* hover color */
  transform: scale(1.08);
  outline: none;
}
.rating-star:focus-visible {
  box-shadow: 0 0 0 3px rgba(241, 181, 10, 0.18);
  border-radius: 4px;
}
.rating-star.active {
  color: #f5b50a;
  transform: scale(1.08);
}
.btn-submit { background:#007bff; color:#fff; padding:10px 14px; border:none; border-radius:6px; cursor:pointer; }
.alert { padding:10px; border-radius:6px; margin-bottom:12px; }
.alert-error { background:#ffe6e6; color:#a00; }
.alert-success { background:#e6ffed; color:#0a6; }
.char-count { text-align:right; color:#666; font-size:0.85rem; margin-top:6px; }
.form-hint { color:#666; margin-top:12px; }

.required-badge { color:#d00; margin-left:8px; font-size:0.85rem; }
.optional-note { color:#666; margin-left:8px; font-size:0.9rem; }
